import React, { Fragment } from 'react';
import Select from 'react-select';
import { Label } from 'reactstrap';
import StudentsTable from './Components/students-table';
import fb from './firebase.service';

class SelectMentorSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: null,
      user: null,
      todos: [],
      messages: [],
    };

    fb.auth.onAuthStateChanged(user =>
      this.setState({ user: user
        ? { displayName: user.displayName, photoURL: user.photoURL }
        : null
      }));

  };

  handleChange (selectedOption) {
    this.setState({ selectedOption: selectedOption.key });
  }

  login = () => {
    fb.login().then(({ user }) => {
      console.log(this);
      this.setState({ user });
    }).catch(function(error) {
      var errorMessage = error.message;
      console.log(errorMessage);
    });
  }

  logout = () => {
    fb.logout().then(() => {
      this.setState({ user: null });
    }).catch(function(error) {
      var errorMessage = error.message;
      console.log(errorMessage);
    });
  }

  render() {
    const { selectedOption } = this.state;
    const { user } = this.state;
    return (
      <Fragment>
        <div className="App">
          <header className="App-header">
            <section className="login__wrapper">
              {user && <img src={user.photoURL} alt='avatar' />}
              {user && <p>{user.displayName}</p>}
              {user
                ? <button onClick={this.logout}>Logout</button>
                : <button onClick={this.login}>Login</button>
              }
            </section>
          </header>
          <main>
        <Label for="mentorSelect">Select Mentor</Label>
        <Select value={selectedOption}
          onChange={this.handleChange}
          options={this.props.dataFromFile.mentorsStudents.map((mentor) => { return { key: mentor.id, value: mentor.mentor, label: mentor.mentor } })}
        />
        <StudentsTable tasks={this.props.dataFromFile.tasks} score={this.props.dataFromFile.student} studentsOfMentor={this.state.selectedOption != null ? this.props.dataFromFile.mentorsStudents[this.state.selectedOption].students : []} />

     </main>
     </div>
     </Fragment>
    )
  }
}


export default SelectMentorSection;
