import React from 'react';

let id = 0;

class StudentsTable extends React.Component {
  createRow = (task) => {
    return this.props.studentsOfMentor.map(student => {console.log(task); console.log(this.props.score[student]); console.log(this.props.score[student].includes(task)); return <td key={id++}>{`${this.props.score[student]
    .includes(task.trim())}`}</td>});
  }

  render() {
    return (
      <div>
        <table>
          <tbody>
            <tr>
              <th>Task\Student</th>
              { this.props.studentsOfMentor.map(record => { return <th key={id++}>{record}</th>})}
            </tr>
            { this.props.tasks.map(task => { return <tr key={id++}><td>{task.task}</td>{this.createRow(task.task)}</tr> })}
          </tbody>
        </table>
      </div>

    )
  }

}


export default StudentsTable;
